let newWorker;
export default function registerServiceWorker() {
    if ('serviceWorker' in navigator) {
        console.log("register service worker");
        navigator.serviceWorker.register(`pipelinesw.js`).then(function (reg) {
            reg.addEventListener('updatefound', () => {
                // A wild service worker has appeared in reg.installing!
                newWorker = reg.installing;
                newWorker.addEventListener('statechange', () => {
                  console.log("Inside state change event");
                  console.log("current state---"+newWorker.state);
                  // Has network.state changed?
                  switch (newWorker.state) {
                    case 'installed':
                      console.log("controller value---"+navigator.serviceWorker.controller);
                      if (navigator.serviceWorker.controller) {
                        // new update available
                        console.log("Going to call show update bar method");
                        // showUpdateBar();
                        // forceSWupdate();
                        alert("New version update available.Please refresh the page to see the changes!!!");
                        window.location.reload();
                      }
                      // No update available
                      break;
                  }
                });
              });
            // if (navigator.serviceWorker.controller) {
            //     // console.log(`This page is currently controlled by: ${navigator.serviceWorker.controller}`);
            //     alert("service worker update available");
            //     console.log("my service worker object hurrey---"+navigator.serviceWorker.controller);
            //   } else {
            //     console.log('This page is not currently controlled by a service worker---'+reg.active);
            //   }
            console.log("It worked for me");
        }).catch(function (err) {
            console.log("Error", err)
        })
    }else{
        console.log("service worker already registered");
    }
}